<template>
  <div class="container">
    <div class="logo">
      <img src="@/assets/weight_control_logo.png" alt="">
    </div>
    <h2 class="caption" style="font-weight: 700">Задайте ваши параметры</h2>
    <form @submit.prevent="setParams" class="form">
      <span class="caption">Ваш вес на данный момент:</span>
      <div class="inputs__wrap">
        <input
            type="text"
            v-model.trim="currentWeight"
            placeholder="Ваш вес"
            @blur="$v.currentWeight.$touch()"
            :class="{invalid: ($v.currentWeight.$dirty && !$v.currentWeight.required) || ($v.currentWeight.$dirty && !$v.currentWeight.numbers)}"
        >
        <small
            v-if="$v.currentWeight.$dirty && !$v.currentWeight.numbers"
        >
          Введите число в формате 00.0
        </small>
      </div>
      <span class="caption">Желаемый вес:</span>
      <div class="inputs__wrap">
        <input
            type="text"
            v-model.trim="wishWeight"
            placeholder="Желаемый вес"
            @blur="$v.wishWeight.$touch()"
            :class="{invalid: ($v.wishWeight.$dirty && !$v.wishWeight.required) || ($v.wishWeight.$dirty && !$v.wishWeight.numbers)}"
        >
        <small
            v-if="$v.currentWeight.$dirty && !$v.currentWeight.numbers"
        >
          Введите число в формате 00.0
        </small>
      </div>
      <span class="caption">Ваш рост:</span>
      <div class="inputs__wrap">
        <input
            type="text"
            v-model.trim="yourHeight"
            placeholder="Ваш рост"
            @blur="$v.yourHeight.$touch()"
            :class="{invalid: ($v.yourHeight.$dirty && !$v.yourHeight.required) || ($v.yourHeight.$dirty && !$v.yourHeight.numbers)}"
        >
        <small
            v-if="$v.currentWeight.$dirty && !$v.currentWeight.numbers"
        >
          Введите число в формате 00.0
        </small>
      </div>
      <button type="submit" class="dws-submit">Задать</button>
    </form>
  </div>
</template>

<script>
import {required, helpers} from 'vuelidate/lib/validators'
import dateFilter from "../filters/date.filter";
const numbers = helpers.regex('numbers', /^[0-9]*[.]?[0-9]+$/)

export default {
  name: "SetParams",
  data: () => ({
    currentWeight: null,
    wishWeight: null,
    yourHeight: null
  }),
  validations: {
    currentWeight: {
      required,
      numbers
    },
    wishWeight: {
      required,
      numbers
    },
    yourHeight: {
      required,
      numbers
    }
  },
  methods: {
    async setParams() {
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }
      let date = dateFilter(this.date, 'date')
      let time = dateFilter(this.date, 'time')
      let day = dateFilter(this.date, 'day')
      const formData = {
        date,
        time,
        day,
        weight: Number(this.currentWeight),
        height: Number(this.yourHeight),
        wishWeight: Number(this.wishWeight)
      }
      await this.$store.dispatch('setValueToDatabase', formData)
      this.$router.push('/your-data')
    }
  }
}
</script>

